<template>
      <div @click="onHeaderClick">
            <!--  -->
            <transition name="ivue-select-fade">
                  <span
                        :class="defaultDisplayClasses"
                        v-if="defaultDisplayValue"
                  >{{defaultDisplayValue}}</span>
            </transition>
            <!--  -->
            <transition-group name="multiple-transition" v-if="selectedMultiple.length > 0">
                  <div
                        :class="[`${prefixCls}-selection-multiple`]"
                        v-for="item in selectedMultiple"
                        :key="item.label"
                  >
                        <span>{{item.label}}</span>
                        <IvueIcon
                              :class="[`${prefixCls}-selection-multiple-close`]"
                              @click.native.stop="removeSelectItem(item)"
                        >{{multipleCloseIcon}}</IvueIcon>
                  </div>
            </transition-group>
            <!--  -->
            <input
                  type="text"
                  v-if="filterable"
                  :class="[`${prefixCls}-input-filter`,{'ivue-select-input-filter-placeholder': showPlaceholder}]"
                  :style="inputStyle"
                  :placeholder="showPlaceholder ? placeholder : ''"
                  spellcheck="false"
                  autocomplete="off"
                  v-model="filterQuery"
                  @focus="onInputFocus"
                  @blur="onInputFocus"
                  @keydown="resetInputState"
                  @keydown.delete="handleInputDelete"
                  ref="input"
            >
            <!--  -->
            <IvueIcon :class="[`${prefixCls}-arrow`]" v-if="!resetSelect">{{arrowDownIcon}}</IvueIcon>
            <!--  -->
            <IvueIcon
                  :class="[`${prefixCls}-arrow`,`${prefixCls}-clear`]"
                  v-if="resetSelect"
                  @click.native.stop="onClear"
                  @mousedown.native.stop="onClear"
            >{{resetSelectIcon}}</IvueIcon>
      </div>
</template>

<script>
import IvueIcon from '../ivue-icon';

// 
import EmitterEvent from '../../utils/mixins/emitter-event';

const prefixCls = 'ivue-select';

export default {
      name: 'ivue-select-head',
      mixins: [EmitterEvent],
      props: {
            /*
            * 
            * 
            * @type{Boolean}
            */
            multiple: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type {Array}
            */
            values: {
                  type: Array,
                  default: () => []
            },
            /*
            * 
            * 
            * @type {String}
            */
            placeholder: {
                  type: String,
                  default: ''
            },
            /*
            * 
            * 
            * @type {Function,Boolean}
            */
            clearable: {
                  type: [Function, Boolean],
                  default: false
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            filterable: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            multipleCloseIcon: {
                  type: String
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            arrowDownIcon: {
                  type: String
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            resetSelectIcon: {
                  type: String
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            clearable: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type{String}
            */
            filterQueryProp: {
                  type: String,
                  default: ''
            }
      },
      data () {
            return {
                  prefixCls: prefixCls,
                  /*
                  * 
                  * 
                  * @type { Number}
                  */
                  inputLength: 20,
                  /*
                  * 
                  * 
                  * @type { String}
                  */
                  filterQuery: '',
                  /*
                  * -v-model
                  * 
                  * @type { Boolean}
                  */
                  isInputChange: false
            }
      },
      computed: {
            // class
            defaultDisplayClasses () {
                  const { showPlaceholder, multiple, filterable } = this;

                  return [
                        {
                              [`${prefixCls}-selection-value`]: !showPlaceholder && !multiple && !filterable,
                              [`${prefixCls}-placeholder`]: showPlaceholder && !filterable
                        }
                  ]
            },
            // 
            defaultDisplayValue () {
                  const { values, multiple, selectedSingle, placeholder } = this;

                  const selected = values[0];

                  //   
                  if ((multiple && values.length > 0) || this.filterable) {
                        return '';
                  }

                  return `${selectedSingle}` || placeholder;
            },
            // 
            showPlaceholder () {
                  const { values, multiple } = this;

                  let status = false;
                  if (!multiple) {
                        const value = values[0];

                        //  undefined 
                        if (typeof value === 'undefined' || String(value).trim() === '') {
                              status = true;
                        }
                  }
                  else {
                        if (!values.length > 0) {
                              status = true;
                        }
                  }

                  return status;
            },
            // 
            selectedSingle () {
                  const { values } = this;

                  const selected = values[0];

                  return selected ? selected.label : '';
            },
            // 
            selectedMultiple () {
                  return this.multiple ? this.values : [];
            },
            // 
            inputStyle () {
                  let style = {};

                  if (this.multiple) {
                        if (this.showPlaceholder) {
                              style.width = "100%";
                        }
                        else {
                              style.width = `${this.inputLength}px`;
                        }
                  }

                  return style;
            },
            // 
            resetSelect () {
                  return !this.showPlaceholder && this.clearable;
            }
      },
      methods: {
            // 
            removeSelectItem (value) {
                  //  IvueSelect 
                  this.dispatch('ivue-select', 'on-select-option', value);
            },
            // 
            onInputFocus (e) {
                  this.$emit(e.type === 'focus' ? 'on-input-focus' : 'on-input-blur');
            },
            // 
            resetInputState () {
                  this.inputLength = this.$refs.input.value.length * 12 + 20;
            },
            // 
            handleInputDelete () {
                  const { multiple, selectedMultiple, filterQuery, removeSelectItem } = this;

                  if (multiple && selectedMultiple.length && filterQuery === '') {
                        removeSelectItem(selectedMultiple[selectedMultiple.length - 1]);
                  }
            },
            // 
            onHeaderClick (e) {
                  if (this.filterable && e.target === this.$el) {
                        this.$refs.input.focus();
                  }
            },
            // 
            onClear () {
                  this.$emit('on-clear');
            }
      },
      watch: {
            // 
            values ([value]) {
                  const { filterable, multiple } = this;

                  if (!filterable) {
                        return;
                  }

                  this.isInputChange = true;

                  // 
                  if (multiple) {
                        this.filterQuery = '';
                        // -v-model
                        this.isInputChange = false;
                        return;
                  }

                  if (typeof value === 'undefined' || value === '' || value === null) {
                        this.filterQuery = '';
                  }
                  else {
                        this.filterQuery = value.label;
                  }

                  this.$nextTick(() => {
                        // -v-model
                        this.isInputChange = false;
                  });

            },
            // 
            filterQuery (value) {
                  if (this.isInputChange) {
                        this.isInputChange = false;
                        return;
                  }

                  this.$emit('on-filter-query-change', value)
            },
            // 
            filterQueryProp (filterQuery) {
                  if (filterQuery !== this.filterQuery && this.filterable) {
                        this.filterQuery = filterQuery;
                  }
            }
      },
      components: {
            IvueIcon
      }
}
</script>



