<template>
      <IvueRipple :ivueDisabled="!disabledRipple">
            <li :class="classes"
            @touchend.stop="selectOption"  
            @click.stop="_click"  
            @mousedown.prevent
            @touchstart="_touchstart"
            @touchmove="_touchmove"
            @touchend="_touchend"
            >
                  <slot>
                        {{showLabel}}
                  </slot>
            </li>
      </IvueRipple>
</template>

<script>
import EmitterEvent from '../../utils/mixins/emitter-event';
import IvueRipple from '../ivue-ripple/ivue-ripple';

const prefixCls = 'ivue-select-item';

export default {
      name: "ivue-option",
      componentName: "ivue-select-item",
      mixins: [EmitterEvent],
      props: {
            /*
            * /
            * 
            * @type {null}
            */
            disabledRipple: {
                  type: Boolean,
                  default: true
            },
            /*
            *  value
            * 
            * @type {String, Number}
            */
            value: {
                  type: [String, Number],
                  required: true
            },
            /*
            *  label
            * 
            * @type {String, Number}
            */
            label: {
                  type: [String, Number]
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            selected: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            isFocused: {
                  type: Boolean,
                  default: false
            },
            /*
            * key
            * 
            * @type {Boolean}
            */
            keys: {
                  type: [String, Number]
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            disabled: {
                  type: Boolean,
                  default: false
            }
      },
      data () {
            return {
                  // 
                  touchesStart: [],
                  // 
                  touchesEnd: []
            }
      },
      computed: {
            classes () {
                  return [
                        prefixCls,
                        {
                              [`${prefixCls}-disabled`]: this.disabled,
                              [`${prefixCls}-selected`]: this.selected,
                              [`${prefixCls}-focus`]: this.isFocused
                        }
                  ]
            },
            // label
            showLabel () {
                  return this.label ? this.label : this.value;
            },
            // label
            getLabel () {
                  return this.label || (this.$el && this.$el.textContent);
            }
      },
      methods: {
            // 
            _touchstart (e) {
                  this.touchesStart.push(e.touches[0]);
            },
            // 
            _touchmove (e) {
                  this.touchesEnd = e.touches[0];
            },
            // 
            _touchend () {
                  this.touchesEnd = []
            },
            // PC
            _click () {
                  // 
                  if (this.touchesStart.length !== 0) {
                        return;
                  }

                  // 
                  this.selectOption();
            },
            // 
            selectOption () {
                  // 
                  if (((this.touchesStart.length > 0) && !(this.touchesEnd.length === 0))) {
                        return;
                  }

                  if (this.disabled) {
                        return;
                  }

                  //  IvueSelect 
                  this.dispatch('ivue-select', 'on-select-option', {
                        value: this.value,
                        label: this.getLabel,
                        keys: this.keys
                  });

                  // 
                  this.$emit('on-select-option', {
                        value: this.value,
                        label: this.getLabel,
                        keys: this.keys
                  })
            }
      },
      components: {
            IvueRipple
      }
}
</script>



